import React from 'react';

import { BrowserRouter, Route, Switch } from 'react-router-dom';


import HomePage from '../pages/home.page';

// auth pages
import SignInPage from '../pages/auth/signin.page';
import SignUpPage from '../pages/auth/signup.page';

// application pages
import AppsPage from '../pages/app/apps';
import DevicesPage from '../pages/app/devices';

// extras
import NotFoundPage from '../pages/extras/notFound.page';
import ForbiddenPage from '../pages/extras/notAllowed.page';

// guide
import UserGuide from '../pages/guides/userguide';

// download center
import DownloadCenter from '../pages/downloads/downloadsPage';

// settings
import UserSettings from '../pages/extras/userSettings.page';

class App extends React.Component {
    render() {
        return (
            <BrowserRouter>
                <Switch>
                    <Route
                        exact path="/signin"
                        component={SignInPage}
                    />
                    <Route
                        exact path="/signup"
                        component={SignUpPage}
                    />
                    <Route
                        exact path="/"
                        component={HomePage}
                    />
                    <Route
                        exact path="/lab/apps"
                        component={AppsPage}
                    />
                    <Route
                        exact path="/lab/devices"
                        component={DevicesPage}
                    />
                    <Route
                        exact path="/manual"
                        component={UserGuide}
                    />
                    <Route
                        exact path="/downloads"
                        component={DownloadCenter}
                    />
                    <Route
                        exact path="/settings"
                        component={UserSettings}
                    />
                    <Route
                        exact path="/forbidden"
                        component={ForbiddenPage}
                    />
                    <Route
                        component={NotFoundPage}
                    />
                </Switch>
            </BrowserRouter>
        )
    }
}


export default App;


