import React from 'react';

import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import HomeNavbar from '../../components/navbar/homeNavbar';


class DeleteUserModal extends React.Component {
    deleteUser = async () => {
        let user = localStorage.getItem('user') || sessionStorage.getItem('user');
        let session = localStorage.getItem('session') || sessionStorage.getItem('session');
        fetch(`/api/users/${user}?user=${user}&session=${session}`, {
            method: 'delete'
        }).then(res => res.json())
        .then(json => {
            if(json.err) {
                if(json.err.message) {
                    alert(json.err.message);
                } else {
                    console.log(json.err);
                }
            } else {
                localStorage.removeItem('user');
                localStorage.removeItem('session');
                sessionStorage.removeItem('user');
                sessionStorage.removeItem('session');
                window.location = '/';
            }
        }).catch(err => console.log(err));
    }

    render() {
        return (
            <Modal
                {...this.props}
                aria-labelledby="delete-app-modal"
                dialogClassName="bg-dark"
            >
                <Modal.Header className="bg-dark">
                    <h3>Eliminar Usuario</h3>
                </Modal.Header>
                <Modal.Body className="bg-dark">
                    ¿Esta seguro de que quiere eliminar su cuenta?. Este proceso es irreversible
                </Modal.Body>
                <Modal.Footer className="bg-dark">
                    <Button variant="outline-primary" onClick={this.props.onHide}>
                        Cancelar
                    </Button>
                    <Button onClick={this.deleteUser}>
                        Eliminar
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}


class UserSettings extends React.Component {
    state = {
        name: '',
        email: '',
        password: '',
        showModal: false
    }

    handleOnSubmit = async e => {
        e.preventDefault();
        let data = {};
        if(this.state.name !== '') {
            data.name = this.state.name;
        }
        if(this.state.email !== '') {
            data.email = this.state.email;
        }
        if(this.state.password !== '') {
            data.password = this.state.password;
        }
        await this.updateUser(data);
    }

    updateUser = async (data) => {
        let user = localStorage.getItem('user') || sessionStorage.getItem('user');
        let session = localStorage.getItem('session') || sessionStorage.getItem('session');
        let res = await fetch(`/api/users/${user}?user=${user}&session=${session}`, {
            method: 'put', headers: {
                'Content-Type': 'application/json'
            }, body: JSON.stringify(data)
        });

        let resData = await res.json();
        if(resData.err) {
            alert(resData.err);
        } else {
            window.location = '/settings'
        }
    }

    handleDataChanged = e => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    render() {
        return (
            <React.Fragment>
                <HomeNavbar />

                <Container className="mt-5 p-5">
                    <Form onSubmit={this.handleOnSubmit}>
                        <Form.Group controlId="formBasicUserName">
                            <Form.Label>Usuario</Form.Label>
                            <Form.Control autoComplete="new-user-name" type="text" placeholder="Nombre de usuario" name="name" onChange={this.handleDataChanged} />
                        </Form.Group>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>E-mail</Form.Label>
                            <Form.Control autoComplete="off" type="email" placeholder="Correo electrónico" name="email" onChange={this.handleDataChanged} />
                        </Form.Group>
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Contraseña</Form.Label>
                            <Form.Control autoComplete="new-password" type="password" placeholder="Contraseña" name="password" onChange={this.handleDataChanged} />
                        </Form.Group>
                        <Row noGutters>
                            <Col>
                                <Button variant="outline-primary" onClick={() => this.setState({showModal: true})}>
                                    Eliminar cuenta
                                </Button>
                                <Button className="ml-1" variant="primary" type="submit">
                                    Actualizar
                                </Button>
                            </Col>
                        </Row>
                    </Form>
                </Container>

                <DeleteUserModal
                    show={this.state.showModal}
                    onHide={() => this.setState({showModal: false})}
                />

            </React.Fragment>
        )
    }
}

export default UserSettings;


