import React from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'

import Dropdown from 'react-bootstrap/Dropdown'
import Spinner from 'react-bootstrap/Spinner'

import { Link, NavLink } from 'react-router-dom';

// import icons
import kel_morianIcon from '../styles/kel-morian.png';
import toolsIcon from '../styles/img/tools.svg';
import terminalIcon from '../styles/img/terminal.svg';
import messageIcon from '../styles/img/envelope.svg'
import deviceIcon from '../styles/img/cpu.green.svg'
import appIcon from '../styles/img/app.green.svg'
import gearIcon from '../styles/img/gear.green.svg'


class AppNavbar extends React.Component {
    state = {
        isLoggedIn: false,
        user: {},
        loadingUser: true
    }

    clearSession() {
        localStorage.removeItem('user');
        localStorage.removeItem('session');
        sessionStorage.removeItem('user');
        sessionStorage.removeItem('session');
    }

    loadUserData = async () => {
        let user = localStorage.getItem('user') || sessionStorage.getItem('user');
        let session = localStorage.getItem('session') || sessionStorage.getItem('session');

        let res = await fetch(`/api/users/${user}?user=${user}&session=${session}`, {
            method: 'get'
        });

        let response = await res.json();

        if (response.err) {
            this.setState({
                isLoggedIn: false,
                user: {}
            });
            this.clearSession();
            window.location = '/signin'
        } else {
            this.setState({
                isLoggedIn: true,
                user: {
                    name: response.user.name,
                    id: response.user._id,
                    session: session
                }
            });
        }
        this.setState({
            loadingUser: false
        });
    }

    logoutHandler = async () => {
        try {
            let res = await fetch(`/api/logout?session=${this.state.user.session}&user=${this.state.user.id}`, {
                method: 'post'
            });
            let response = await res.json();
            if(response.err) {
                alert(response.err)
            } else {
                this.clearSession();
                window.location = '/';
            }
        } catch(err) {
            console.log(err);
        }
    }

    componentDidMount() {
        this.loadUserData();
    }

    render() {
        return (
            <Navbar variant="dark" bg="dark" expand="lg">

                <Navbar.Brand >
                    <Link to="/">
                        <img height="30" src={kel_morianIcon} alt="icon"></img>
                    </Link>
                </Navbar.Brand>

                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto" defaultActiveKey="/lab/apps">
                        <Nav.Item>
                            <NavLink className="nav-link" to="/lab/apps">
                                <span><img src={appIcon} alt="" /></span> Aplicaciones
                            </NavLink>
                        </Nav.Item>
                        <Nav.Item>
                            <NavLink className="nav-link" to="/lab/devices">
                                <span><img src={deviceIcon} alt="" /></span> Dispositivos
                            </NavLink>
                        </Nav.Item>
                        <Nav.Item>
                            <Dropdown drop="down">
                                <Dropdown.Toggle variant="link" id="sessionDropDown" className="text-light">
                                    <span><img src={toolsIcon} alt="" /></span> Herramientas
                                </Dropdown.Toggle>
                                <Dropdown.Menu className="bg-dark">
                                    <Dropdown.Item className="text-light bg-dark">
                                        <span><img src={terminalIcon} alt="" /></span> DB terminal
                                    </Dropdown.Item>
                                    <Dropdown.Item className="text-light bg-dark">
                                        <span><img src={messageIcon} alt="" /></span> Message caster
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </Nav.Item>
                    </Nav>
                    <Nav inline="true">
                        {this.state.loadingUser &&
                            <Spinner animation="border" role="status">
                                <span className="sr-only">Loading...</span>
                            </Spinner>
                        }
                        {!this.state.loadingUser &&
                            <Dropdown drop="left">
                                <Dropdown.Toggle variant="primary" id="sessionDropDown">
                                    {this.state.user.name}
                                </Dropdown.Toggle>

                                <Dropdown.Menu className="bg-dark">
                                    <Dropdown.Item as={Link} to="/settings" className="text-light bg-dark">
                                        <span><img src={gearIcon} alt="" /></span> Ajustes
                                    </Dropdown.Item>
                                    <Dropdown.Divider />
                                    <Dropdown.Item className="text-light bg-dark" onClick={this.logoutHandler}>
                                        Cerrar sesión
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        }
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default AppNavbar



