import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';


class CreateAppModal extends React.Component {
    state = {
        name: '',
        description: '',
        privacyLevel: "1"
    }

    onCreateClicked = async () => {
        try {
            let user = localStorage.getItem('user') || sessionStorage.getItem('user');
            let session = localStorage.getItem('session') || sessionStorage.getItem('session');

            let res = await fetch(`/api/user-applications?user=${user}&session=${session}`, {
                method: 'post',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    userID: user,
                    name: this.state.name,
                    description: this.state.description,
                    privacyLevel: (this.state.privacyLevel === '2'? 'PRIVATE': 'PUBLIC')
                })
            });
            let response = await res.json();

            if(response.err) {
                alert(response.err.message);
            } else {
                this.props.onCreated();
                this.props.onHide();
            }
        } catch (err) {
            console.log(err);
        }
    }

    onDataChanged = e => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    render() {
        return (
            <Modal
                {...this.props}
                aria-labelledby="create-app-modal"
                centered
            >
                <Modal.Header className="bg-dark">
                    <Modal.Title id="create-app-modal">
                        <h3>Crear una nueva aplicación</h3>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body className="bg-dark">
                    <Form onSubmit={() => {}}>
                        <Form.Group>
                            <Form.Label>Nombre</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="Nombre de la aplicación"
                                name="name"
                                onChange={this.onDataChanged}
                            />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Descripción</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="Descripción de la aplicación"
                                name="description"
                                onChange={this.onDataChanged}
                            />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Privacidad</Form.Label>
                            <Form.Control
                                as="select"
                                placeholder="Descripción de la aplicación"
                                name="privacyLevel"
                                onChange={this.onDataChanged}
                            >
                                <option selected value="0">Seleccionar...</option>
                                <option value="1" >Pública</option>
                                <option value="2">Privada</option>
                            </Form.Control>
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer className="bg-dark">
                    <Button variant="primary" onClick={this.props.onHide}>
                        Cancelar
                    </Button>
                    <Button onClick={this.onCreateClicked}>
                        Crear
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

export default CreateAppModal;


