import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Spinner from 'react-bootstrap/Spinner';
import AppNavbar from '../../components/navbar/appNavbar';
import DeviceCard from '../../components/cards/deviceCard';
import CreateDeviceModal from '../../components/create/createDevice';

// import icon
import plusIcon from '../../components/styles/img/plus.svg';

class DevicesPage extends React.Component {
    state = {
        loading: true,
        devices: [],
        showCreateModal: false
    }

    getDevices = async () => {
        this.setState({
            loading: true
        });
        try {
            let user = localStorage.getItem('user') || sessionStorage.getItem('user');
            let session = localStorage.getItem('session') || sessionStorage.getItem('session');

            let res = await fetch(`/api/devices?user=${user}&session=${session}`);
            let response = await res.json();
            let devices = response.devices;

            this.setState({
                loading: false,
                devices
            });

        } catch (err) {
            console.log(err);
        }
    }

    componentDidMount() {
        this.getDevices();
    }

    render() {
        return (
            <div>
                <AppNavbar />

                <Container className="mt-3">
                    <Row noGutters className="justify-content-end">
                        <Col>
                            <h3>Dispositivos</h3>
                        </Col>
                        <Col md={1}>
                            <Button onClick={() => this.setState({
                                showCreateModal: true
                            })}>
                                <img src={plusIcon} width="25px" alt="" />
                            </Button>
                        </Col>
                    </Row>
                </Container>
                <Container fluid className="mt-3">
                    <Row noGutters className="justify-content-around mt-1">
                        {this.state.loading &&
                            <Spinner animation="border" role="status">
                                <span className="sr-only">Loading...</span>
                            </Spinner>
                        }
                        {!this.state.loading &&
                            this.state.devices.map((device, index, array) => {
                                return (
                                    <Col md={4} className="mt-3">
                                        <DeviceCard
                                            name={device.name}
                                            description={device.description}
                                            apikey={device.apiKey}
                                            app={device.app}
                                            id={device._id}
                                            onUpdated={this.getDevices}
                                        />
                                    </Col>
                                )
                            })
                        }
                    </Row>
                </Container>
                <CreateDeviceModal
                    show={this.state.showCreateModal}
                    onHide={() => this.setState({
                        showCreateModal: false
                    })}
                    onCreated={this.getDevices}
                />
            </div>
        )
    }
}

export default DevicesPage;


