import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

class AppDetailsModal extends React.Component {
    state = {
        edited: false
    }

    onDataChanged = e => {
        this.setState({
            edited: true,
            [e.target.name]: e.target.value,
            appFiles: null
        });
    }

    onUpdateClicked = async () => {
        if(this.state.appFiles !== null && this.state.appFiles !== undefined) {
            this.uploadFile();
            try {
                let user = localStorage.getItem('user') || sessionStorage.getItem('user');
                let session = localStorage.getItem('session') || sessionStorage.getItem('session');
    
                let formdata = {};

                if(this.state.name !== '') {
                    formdata.name = this.state.name;
                }
                if(this.state.description !== '') {
                    formdata.description = this.state.description;
                }
                if(this.state.privacyLevel !== '' && this.state.privacyLevel !== '0') {
                    formdata.privacyLevel = this.state.privacyLevel === '1' ? 'PUBLIC' : 'PRIVATE';
                }
    
                let res = await fetch(`/api/user-applications/${this.props.id}?user=${user}&session=${session}`, {
                    method: 'put',
                    headers: {
                        'Content-Type': 'application/json'
                    }, body: JSON.stringify(formdata)
                });
                
                
                let response = await res.json();
                if (response.err) {
                    alert(response.err);
                }
            }catch(err) {
                console.log(err);
            }
        } else {
            try {
                let user = localStorage.getItem('user') || sessionStorage.getItem('user');
                let session = localStorage.getItem('session') || sessionStorage.getItem('session');
    
                let formdata = {
                    name: this.state.name,
                    description: this.state.description,
                    privacyLevel: this.state.privacyLevel === '1' ? 'PUBLIC' : 'PRIVATE'
                }
    
                let res = await fetch(`/api/user-applications/${this.props.id}?user=${user}&session=${session}`, {
                    method: 'put',
                    headers: {
                        'Content-Type': 'application/json'
                    }, body: JSON.stringify(formdata)
                });
                
                
                let response = await res.json();
                if (response.err) {
                    if(response.err.message) {
                        alert(response.err.message);
                    } else{
                        console.log(response.err);
                    }
                } else {
                    this.props.onUpdated();
                }
            }catch(err) {
                console.log(err);
            }
        }
    }

    uploadFile = () => {
        let user = localStorage.getItem('user') || sessionStorage.getItem('user');
        let session = localStorage.getItem('session') || sessionStorage.getItem('session');

        let fileForm = new FormData();
        fileForm.append('appFiles', this.state.appFiles);

        console.log(fileForm);

        fetch(`/api/user-applications/${this.props.id}?user=${user}&session=${session}`, {
            method: 'put',
            body: fileForm
        })
        .then(res => res.json())
        .then(json => {
            console.log(json)
            this.props.onUpdated();
        })
        .catch(err => {
            if(err.message) {
                alert(err.message)
            } else {
                console.log(err);
            }
        });
    }

    componentDidMount () {
        this.setState({
            ...this.props
        });
        this.setState({
            privacyLevel: (this.props.privacyLevel === 1? '1' : '2')
        });
    }

    render() {
        return (
            <Modal
                {...this.props}
                aria-labelledby="app-details-modal"
                centered
                size="lg"
            >
                <Modal.Header className="bg-dark">
                    <Modal.Title id="app-details-modal">
                        <h3>{this.props.name}</h3>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body className="bg-dark">
                    <p>ID = {this.props.id}</p>
                    <p>API key = {this.props.apikey}</p>
                    <Form onSubmit={this.onUpdateClicked}>
                        <Form.Group>
                            <Form.Label>Nombre</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder={this.props.name}
                                name="name"
                                onChange={this.onDataChanged}
                            />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Descripción</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder={this.props.description}
                                name="description"
                                onChange={this.onDataChanged}
                            />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Privacidad</Form.Label>
                            <Form.Control
                                as="select"
                                placeholder="Descripción de la aplicación"
                                name="privacyLevel"
                                onChange={this.onDataChanged}
                            >
                                <option value="0">Seleccionar...</option>
                                {this.state.privacyLevel === '2' &&
                                    <React.Fragment>
                                        <option value="1" >Pública</option>
                                        <option selected value="2">Privada</option>
                                    </React.Fragment>
                                }
                                {this.state.privacyLevel === '1' &&
                                    <React.Fragment>
                                        <option selected value="1" >Pública</option>
                                        <option value="2">Privada</option>
                                    </React.Fragment>
                                }
                            </Form.Control>
                        </Form.Group>
                        <Form.Group>
                            <Form.File id="appFiles" label="Cargar archivos de la aplicación" onChange={e => {
                                this.setState({
                                    appFiles: e.target.files[0]
                                });
                            }}/>
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer className="bg-dark">
                    <Button variant="outline-primary" onClick={this.props.onHide}>
                        Cancelar
                    </Button>
                    <Button variant="primary" onClick={this.onUpdateClicked}>
                        Actualizar
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

export default AppDetailsModal;


