import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Spinner from 'react-bootstrap/Spinner';
import AppNavbar from '../../components/navbar/appNavbar';
import AppCard from '../../components/cards/appCard';
import CreateAppModal from '../../components/create/createApp';

// import icon
import plusIcon from '../../components/styles/img/plus.svg';

class AppsPage extends React.Component {
    state = {
        loading: true,
        apps: [],
        showCreateModal: false
    }

    getApps = async () => {
        this.setState({
            loading: true
        });
        try {
            let user = localStorage.getItem('user') || sessionStorage.getItem('user');
            let session = localStorage.getItem('session') || sessionStorage.getItem('session');

            let res = await fetch(`/api/user-applications?user=${user}&session=${session}`);
            let response = await res.json();
            let apps = response.apps;

            this.setState({
                loading: false,
                apps
            });
        } catch (err) {
            console.log(err);
        }
    }

    componentDidMount() {
        this.getApps();
    }

    render() {
        return (
            <div>
                <AppNavbar />

                <Container className="mt-3">
                    <Row noGutters className="justify-content-end">
                        <Col>
                            <h3>Aplicaciones</h3>
                        </Col>
                        <Col md={1}>
                            <Button onClick={() => this.setState({
                                showCreateModal: true
                            })}>
                                <img src={plusIcon} width="25px" alt="" />
                            </Button>
                        </Col>
                    </Row>
                </Container>
                <Container fluid className="mt-3">
                    <Row noGutters className="justify-content-around mt-1">
                        {this.state.loading &&
                            <Spinner animation="border" role="status">
                                <span className="sr-only">Loading...</span>
                            </Spinner>
                        }
                        {!this.state.loading &&
                            this.state.apps.map((app, index, array) => {
                                return (
                                    <Col md={4} className="mt-3">
                                        <AppCard
                                            name={app.name}
                                            description={app.description}
                                            privacyLevel={app.privacyLevel}
                                            apikey={app.apiKey}
                                            id={app._id}
                                            onUpdated={this.getApps}
                                            />
                                    </Col>
                                )
                            })
                        }
                    </Row>
                </Container>

                <CreateAppModal
                    show={this.state.showCreateModal}
                    onHide={() => this.setState({
                        showCreateModal: false
                    })}
                    onCreated={this.getApps}
                />
            </div>
        );
    }
}

export default AppsPage;



