import React from 'react';

import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

import AppDetailsModal from './appDetailsModal';

import DeleteModal from './appDeleteConfirmation';


// import delete icon
import TrashIcon from '../styles/img/trash.svg';

class AppCard extends React.Component {
    state = {
        showModal: false,
        deleteModal: false
    }

    render() {
        return (
            <React.Fragment>
            <Card style={{ width: '23rem' }} bg="dark">
                <Card.Header as="h3">{this.props.name}</Card.Header>
                <Card.Body>
                    <Card.Title>Descripción</Card.Title>
                    <Card.Text>
                        {this.props.description}
                    </Card.Text>
                    <Button variant="outline-primary" onClick={() => this.setState({deleteModal: true})}>
                        <span><img alt="" src={TrashIcon}/></span>
                        Eliminar
                    </Button>
                    <Button className="ml-1" variant="outline-primary" onClick={() => this.setState({
                        showModal: true
                    })}>Detalles</Button>
                    <Button className="ml-1" variant="outline-primary" onClick={() => {
                        if(this.props.privacyLevel === 1) {
                            // public
                            window.open(`/api/render/${this.props.id}`, '_blank');
                        } else {
                            // private
                            let user = localStorage.getItem('user') || sessionStorage.getItem('user');
                            let session = localStorage.getItem('session') || sessionStorage.getItem('session');
                            window.open(`/api/render/${this.props.id}?user=${user}&session=${session}`, '_blank');
                        }
                    }}>Abrir aplicación</Button>
                </Card.Body>
            </Card>
            
            <AppDetailsModal
                show={this.state.showModal}
                onHide={() => this.setState({
                    showModal: false
                })}
                name={this.props.name}
                description={this.props.description}
                privacyLevel={this.props.privacyLevel}
                apikey={this.props.apikey}
                id={this.props.id}
                onUpdated={this.props.onUpdated}
            />

            <DeleteModal
                show={this.state.deleteModal}
                onHide={() => this.setState({deleteModal: false})}
                id={this.props.id}
                name={this.props.name}
                onDeleted={this.props.onUpdated}
            />

            </React.Fragment>
        )
    }
}

export default AppCard


