import React from 'react';

import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

import DeviceDetailsModal from './deviceDetailsModal';
import DeleteModal from './deviceDeleteConfirmation';

// import delete icon
import TrashIcon from '../styles/img/trash.svg';

class DeviceCard extends React.Component {
    state = {
        showModal: false,
        deleteModal: false
    }

    render() {
        return (
            <React.Fragment>
                <Card style={{ width: '23rem' }} bg="dark">
                    <Card.Header as="h3">{this.props.name}</Card.Header>
                    <Card.Body>
                        <Card.Title>Descripción</Card.Title>
                        <Card.Text>
                            {this.props.description}
                        </Card.Text>
                        <Button variant="outline-primary" onClick={() => this.setState({deleteModal: true})}>
                            <span><img alt="" src={TrashIcon}/></span>
                            Eliminar
                        </Button>
                        <Button className="ml-1" variant="outline-primary" onClick={() => this.setState({
                            showModal: true
                        })}>Detalles</Button>
                    </Card.Body>
                </Card>
                <DeviceDetailsModal
                    show={this.state.showModal}
                    onHide={() => this.setState({
                        showModal: false
                    })}
                    name={this.props.name}
                    description={this.props.description}
                    apikey={this.props.apikey}
                    id={this.props.id}
                    app={this.props.app}
                    onUpdated={this.props.onUpdated}
                />

                <DeleteModal
                    show={this.state.deleteModal}
                    onHide={() => this.setState({deleteModal: false})}
                    id={this.props.id}
                    name={this.props.name}
                    onDeleted={this.props.onUpdated}
                />

            </React.Fragment>
        )
    }
}

export default DeviceCard


