import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

class DeviceDetailsModal extends React.Component {
    state = {
        edited: false,
        apps: []
    }

    onDataChanged = e => {
        this.setState({
            edited: true,
            [e.target.name]: e.target.value
        });
    }

    onUpdateClicked = async () => {
        try {
            let user = localStorage.getItem('user') || sessionStorage.getItem('user');
            let session = localStorage.getItem('session') || sessionStorage.getItem('session');

            let formdata = {};

            if(this.state.name !== '') {
                formdata.name = this.state.name;
            }

            if(this.state.description !== '') {
                formdata.description = this.state.description;
            }

            if(this.state.app !== '0') {
                formdata.app = this.state.app;
            }

            let res = await fetch(`/api/devices/${this.props.id}?user=${user}&session=${session}`, {
                method: 'put',
                headers: {
                    'Content-Type': 'application/json'
                }, body: JSON.stringify(formdata)
            });
            
            
            let response = await res.json();
            if (response.err) {
                if(response.err.message) {
                    alert(response.err.message);
                } else{
                    console.log(response.err);
                }
            } else {
                this.props.onUpdated();
            }
        }catch(err) {
            console.log(err);
        }
    }

    getApps = async () => {
        try {
            let user = localStorage.getItem('user') || sessionStorage.getItem('user');
            let session = localStorage.getItem('session') || sessionStorage.getItem('session');

            let res = await fetch(`/api/user-applications?user=${user}&session=${session}`);
            let response = await res.json();
            let apps = response.apps;

            this.setState({
                apps
            });

        } catch (err) {
            console.log(err);
        }
    }

    componentDidMount () {
        this.setState({
            ...this.props
        });
        this.getApps();
    }

    render() {
        return (
            <Modal
                {...this.props}
                aria-labelledby="device-details-modal"
                centered
                size="lg"
            >
                <Modal.Header className="bg-dark">
                    <Modal.Title id="device-details-modal">
                        <h3>{this.props.name}</h3>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body className="bg-dark">
                    <p>ID = {this.props.id}</p>
                    <p>API key = {this.props.apikey}</p>
                    <Form onSubmit={this.onUpdateClicked}>
                        <Form.Group>
                            <Form.Label>Nombre</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder={this.props.name}
                                name="name"
                                onChange={this.onDataChanged}
                            />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Descripción</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder={this.props.description}
                                name="description"
                                onChange={this.onDataChanged}
                            />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Aplicación</Form.Label>
                            <Form.Control
                                as="select"
                                placeholder="Descripción de la aplicación"
                                name="app"
                                onChange={this.onDataChanged}
                            >
                                <option value="0">Seleccionar...</option>
                                {
                                    this.state.apps.map( (app, index, array) => {
                                        if(this.props.app === app._id) {
                                            return (
                                                <option selected value={app._id} >{app.name}</option>
                                            )
                                        } else {
                                            return (
                                                <option value={app._id} >{app.name}</option>
                                            )
                                        }
                                    })
                                }
                            </Form.Control>
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer className="bg-dark">
                    <Button variant="outline-primary" onClick={this.props.onHide}>
                        Cancelar
                    </Button>
                    <Button variant="primary" onClick={this.onUpdateClicked}>
                        Actualizar
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

export default DeviceDetailsModal;


