import React from 'react';
import HomeNavbar from '../../components/navbar/homeNavbar';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';


class DownloadCenter extends React.Component {
    render() {
        return (
            <div>
                <HomeNavbar
                    islogged={this.props.islogged}
                    user={this.props.user}
                    clearSession={this.props.clearSession}
                />
                <Container fluid className="mt-3">
                    <Row noGutters className="align-items-center justify-content-center">
                        <Col md={4}>
                            <h2>
                                Centro de descargas
                            </h2>
                        </Col>
                    </Row>
                    <Row className="ml-5 mr-5">
                        <Table striped bordered hover size="sm">
                            <thead>
                                <tr>
                                    <th>API</th>
                                    <th>Plataforma</th>
                                    <th>Nombre del archivo</th>
                                    <th>Checksum MD5</th>
                                    <th>Checksum SHA256</th>
                                    <th>Descargar</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td rowSpan={3}>WSTT</td>
                                    <td>Windows 64 bits</td>
                                    <td>wstt-MSVC2019-64bit.zip</td>
                                    <td>9cede96b5a3dde34f66879ac04c1d1b7</td>
                                    <td>5fc82aa1001b98e459bd688c5cba0751e758450634a149de01e51e82ce19f249</td>
                                    <td><a className="btn btn-outline-secondary" href="/libraries/wstt-MSVC2017-64bit.zip" download="wstt-MSVC2017-64bit.zip">Descargar</a></td>
                                </tr>
                                <tr>
                                    <td>WebAssembly</td>
                                    <td>wstt-WASM.zip</td>
                                    <td>5f9394a71a641cde814745a781f4f9e5</td>
                                    <td>a6fdbe8cfdb12bb7721c7882eb8f4471d0bc21dfcdf4f9a7b6c9a799c51983d3</td>
                                    <td><a className="btn btn-outline-secondary" href="/libraries/wstt-WASM.zip" download="wstt-WASM.zip">Descargar</a></td>
                                </tr>
                            </tbody>
                        </Table>
                    </Row>
                    <Row className="ml-5 mr-5">
                        <p className="text-muted">
                            Si deseas compilar la API para otras plataformas el código fuente se encuentra disponible en <a href="https://github.com/javiercont97/qt-wstt-client" target="_blank">github</a>.
                            Otra alternativa es incluir el código en tu proyecto.
                        </p>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default DownloadCenter;


