import React from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Dropdown from 'react-bootstrap/Dropdown'
import Spinner from 'react-bootstrap/Spinner'

import { Link, NavLink } from 'react-router-dom';

// import icons
import kel_morianIcon from '../styles/kel-morian.png'
import bookIcon from '../styles/img/book.green.svg'
import joystickIcon from '../styles/img/joystick.green.svg'
import downloadIcon from '../styles/img/download.green.svg'
import gearIcon from '../styles/img/gear.green.svg'


class HomeNavbar extends React.Component {
    state = {
        isLoggedIn: false,
        user: {},
        loadingUser: true
    }

    clearSession() {
        localStorage.removeItem('user');
        localStorage.removeItem('session');
        sessionStorage.removeItem('user');
        sessionStorage.removeItem('session');
    }

    loadUserData = async () => {
        let user = localStorage.getItem('user') || sessionStorage.getItem('user');
        let session = localStorage.getItem('session') || sessionStorage.getItem('session');

        let res = await fetch(`/api/users/${user}?user=${user}&session=${session}`, {
            method: 'get'
        });

        let response = await res.json();

        if(response.err) {
            this.setState({
                isLoggedIn: false,
                user: {}
            });
            this.clearSession();
        } else {
            this.setState({
                isLoggedIn: true,
                user: {
                    name: response.user.name,
                    id: response.user._id,
                    session: session
                }
            });
        }

        this.setState({
            loadingUser: false
        });
    }

    logoutHandler = async () => {
        try {
            let res = await fetch(`/api/logout?session=${this.state.user.session}&user=${this.state.user.id}`, {
                method: 'post'
            });
            let response = await res.json();
            if(response.err) {
                alert(response.err)
            } else {
                this.clearSession();
                window.location = '/';
            }
        } catch(err) {
            console.log(err);
        }
    }

    componentDidMount() {
        this.loadUserData();
    }

    render() {
        return (
            <Navbar variant="dark" bg="dark" expand="lg">
                <Navbar.Brand >
                    <Link to="/">
                        <img height="30" src={kel_morianIcon} alt="icon"></img>
                    </Link>
                </Navbar.Brand>

                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Item>
                            <NavLink to="/manual" className="nav-link">
                                <span><img src={bookIcon} alt="" /></span> Manual de usuario
                            </NavLink>
                        </Nav.Item>
                        <Nav.Item>
                            <NavLink to="/downloads" className="nav-link">
                                <span><img src={downloadIcon} alt="" /></span> Descargas
                            </NavLink>
                        </Nav.Item>
                        {this.state.isLoggedIn &&
                            <Nav.Item>
                                <NavLink to="/lab/apps" className="nav-link">
                                    <span><img src={joystickIcon} alt="" /></span> Playground
                                </NavLink>
                            </Nav.Item>
                        }
                    </Nav>
                    {this.state.loadingUser &&
                        <Spinner animation="border" role="status">
                            <span className="sr-only">Loading...</span>
                        </Spinner>
                    }
                    {!this.state.loadingUser && !this.state.user.name &&
                        <Nav inline="true">
                            <NavLink to="/signin" className="nav-link btn btn-primary text-light">
                                    Iniciar sesión
                            </NavLink>
                        </Nav>
                    }
                    {!this.state.loadingUser && this.state.user.name &&
                        <Dropdown drop="left">
                            <Dropdown.Toggle className="text-light" variant="primary" id="sessionDropDown">
                                {this.state.user.name}
                            </Dropdown.Toggle>

                            <Dropdown.Menu className="bg-dark">
                                <Dropdown.Item as={Link} to="/settings" className="text-light bg-dark">
                                    <span><img src={gearIcon} alt=""/></span> Ajustes
                                </Dropdown.Item>
                                <Dropdown.Divider />
                                <Dropdown.Item className="text-light bg-dark" onClick={this.logoutHandler}>
                                    Cerrar sesión
                                </Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    }
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default HomeNavbar



