import React from 'react'


import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import { Link } from 'react-router-dom';


class SignIn extends React.Component {
    state = {
        name: '',
        password: '',
        keepSession: true,
        platform: navigator.platform
    }

    handleOnSubmit = async e => {
        e.preventDefault();
        let res = await fetch('/api/login', {
            method: 'post', headers: {
                'Content-Type': 'application/json'
            }, body: JSON.stringify(this.state)
        });

        let data = await res.json();
        if(data.err) {
            if(data.err.message) {
                alert(data.err.message);
            } else {
                alert(data.err);
            }
        } else {
            if(this.state.keepSession) {
                localStorage.setItem('user', data.userID);
                localStorage.setItem('session', data.session);
            } else {
                sessionStorage.setItem('user', data.userID);
                sessionStorage.setItem('session', data.session);
            }
            window.location = '/';
        }
    }

    handleDataChanged = e => {
        this.setState({
            [e.target.name]: (e.target.name === 'keepSession'? e.target.checked: e.target.value)
        });
    }

    render() {
        return (
            <Form onSubmit={this.handleOnSubmit}>
                <Form.Group controlId="formBasicUserName">
                    <Form.Label>Usuario</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Nombre de usuario"
                        name="name"
                        onChange={this.handleDataChanged}
                        value={this.state.name}
                    />
                </Form.Group>
                <Form.Group controlId="formBasicPassword">
                    <Form.Label>Contraseña</Form.Label>
                    <Form.Control
                        type="password"
                        placeholder="Contraseña"
                        name="password"
                        onChange={this.handleDataChanged}
                        value={this.state.password}
                    />
                </Form.Group>
                <Form.Group controlId="formBasicCheckbox">
                    <Form.Check
                        type="checkbox"
                        label="Mantener sesión"
                        name="keepSession"
                        custom
                        onChange={this.handleDataChanged}
                        checked={this.state.keepSession}
                    />
                </Form.Group>
                <Row noGutters>
                    <Col>
                        <Button variant="primary" type="submit">
                            Ingresar
                        </Button>
                    </Col>
                    <Col>
                        <Form.Group>
                            <Form.Text className="text-muted">
                                ¿No tienes una cuenta? <Link to="/signup">Registrarse</Link>
                            </Form.Text>
                        </Form.Group>
                    </Col>
                </Row>
            </Form>
        )
    }
}

export default SignIn



