import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';


class AppDeleteModal extends React.Component {
    deleteApp = async () => {
        let user = localStorage.getItem('user') || sessionStorage.getItem('user');
        let session = localStorage.getItem('session') || sessionStorage.getItem('session');
        fetch(`/api/user-applications/${this.props.id}?user=${user}&session=${session}`,{
            method: 'delete'
        })
            .then(res => res.json())
            .then(json => {
                if(json.err) {
                    if(json.err.message) {
                        alert(json.err.message);
                    } else {
                        console.log(json.err);
                    }
                } else {
                    this.props.onDeleted();
                }
            }).catch(err => console.log(err));
    }

    render() {
        return (
            <Modal
                {...this.props}
                aria-labelledby="delete-app-modal"
                dialogClassName="bg-dark"
            >
                <Modal.Header className="bg-dark">
                    <h3>Eliminar aplicación</h3>
                </Modal.Header>
                <Modal.Body className="bg-dark">
                    ¿Desea eliminar la aplicación "{this.props.name}"?
                </Modal.Body>
                <Modal.Footer className="bg-dark">
                    <Button variant="outline-primary" onClick={this.props.onHide}>
                        Cancelar
                    </Button>
                    <Button onClick={this.deleteApp}>
                        Eliminar
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

export default AppDeleteModal;


