import React from 'react'


import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import { Link } from 'react-router-dom';


class SignIn extends React.Component {
    state = {
        name: '',
        password: '',
        email: ''
    }

    handleOnSubmit = async e => {
        e.preventDefault();
        await this.createUser(this.state);
    }

    createUser = async (data) => {
        let res = await fetch('/api/users', {
            method: 'post', headers: {
                'Content-Type': 'application/json'
            }, body: JSON.stringify(data)
        });

        let resData = await res.json();
        if(resData.err) {
            alert(resData.err);
        } else {
            alert('Espere el correo de activación para iniciar sesión');
        }
    }

    handleDataChanged = e => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    render() {
        return (
            <Form onSubmit={this.handleOnSubmit}>
                <Form.Group controlId="formBasicUserName">
                    <Form.Label>Usuario</Form.Label>
                    <Form.Control type="text" placeholder="Nombre de usuario" name="name" onChange={this.handleDataChanged} />
                </Form.Group>
                <Form.Group controlId="formBasicEmail">
                    <Form.Label>E-mail</Form.Label>
                    <Form.Control type="email" placeholder="Correo electrónico" name="email" onChange={this.handleDataChanged} />
                </Form.Group>
                <Form.Group controlId="formBasicPassword">
                    <Form.Label>Contraseña</Form.Label>
                    <Form.Control type="password" placeholder="Contraseña" name="password" onChange={this.handleDataChanged} />
                </Form.Group>
                <Row noGutters>
                    <Col>
                        <Button variant="primary" type="submit">
                            Registrar
                        </Button>
                    </Col>
                    <Col>
                        <Form.Group>
                            <Form.Text className="text-muted">
                                ¿Ya tienes una cuenta? <Link to="/signin">Ingresar</Link>
                            </Form.Text>
                        </Form.Group>
                    </Col>
                </Row>
            </Form>
        )
    }
}

export default SignIn



